import React, { useEffect, useState } from "react";
import { Form, Input, Typography, Space, Alert } from "antd";
import { useAuth } from "../../assistant/Contexts/AuthContext";
import { useNavigate } from "react-router-dom";
import Logo from "./Logo";
import Card from "./Card";
import { AntDesignOutlined, LockOutlined, UserOutlined } from "@ant-design/icons";
import { GradientButton } from "./GradientButton";

const { Title, Text } = Typography;

// Main App component
export default function LoginForm() {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isLoginSuccess, setIsLoginSuccess] = useState(true);
  const { login, isAuthenticated } = useAuth();
  const navigate = useNavigate();

  // If already authenticated, redirect to home page
  useEffect(() => {
    if (isAuthenticated) {
      navigate("/homepage", { replace: true });
    }
  }, [isAuthenticated, navigate]);

  // Function to handle form submission
  const handleOnFinish = async (values) => {
    setIsSubmitting(true);
    const { username, password } = values;
    const isSuccess = await login(username, password);
    setIsSubmitting(false);
    setIsLoginSuccess(isSuccess);
  };

  return (
    <Card>
      <Space direction="vertical" style={{ width: "100%", marginBottom: "24px" }}>
        <Logo />
        <Title level={3} style={{ margin: 0 }}>
          Login into your account
        </Title>
        <Text type="secondary">Where there is no vision, the people perish.</Text>
      </Space>

      {/* Login Form, using Ant Design's Form and Input components. */}
      <Form
        name="login" // Form name
        // initialValues={{ remember: true }} // Initial form values
        onFinish={handleOnFinish} // Callback when form is submitted and validated
        layout="vertical" // Labels are placed above input fields
      >
        {/* Username input field with validation rules. */}
        <Form.Item
          label="Username"
          name="username"
          rules={[{ required: true, message: "Please input your username!" }]}
        >
          <Input placeholder="Your username here" prefix={<UserOutlined />} />
        </Form.Item>

        {/* Password input field with validation rules. */}
        <Form.Item
          label="Password"
          name="password"
          rules={[{ required: true, message: "Please input your Password!" }]}
        >
          <Input.Password placeholder="Your password here" prefix={<LockOutlined />} />
        </Form.Item>

        {/* Login button. */}
        <Form.Item>
          <GradientButton
            type="primary"
            htmlType="submit"
            loading={isSubmitting}
            disabled={isSubmitting}
            block
            size="large"
            icon={<AntDesignOutlined />}
          >
            Login
          </GradientButton>
          {!isLoginSuccess && !isSubmitting && (
            <Alert
              message="Login Failed!"
              description="Invalid username or password!"
              type="error"
              showIcon
              // closable
              style={{ marginTop: 8 }}
            />
          )}
        </Form.Item>
      </Form>
    </Card>
  );
}
