import * as React from "react";
import { Button, Divider, Input, Select, Space } from "antd";
import { AddCircle12Filled } from "@fluentui/react-icons";

const DropdownWithAdd = ({
  options,
  selectPlaceholder,
  newInputPlaceholder,
  value,
  shouldDisableAddNew,
  selectPrefix,
  onAddNew,
  onChange,
}) => {
  const [newVal, setNewVal] = React.useState("");

  const popupRender = (menu) => {
    return (
      <>
        <Space direction="horizontal">
          <Input
            disabled={shouldDisableAddNew}
            placeholder={newInputPlaceholder}
            value={newVal}
            onChange={(e) => {
              setNewVal(e.target.value);
            }}
          />
          <Button
            type="dashed"
            icon={<AddCircle12Filled />}
            disabled={shouldDisableAddNew}
            onClick={() => {
              if (newVal) {
                const val = newVal;
                setNewVal("");
                onAddNew(val);
              }
            }}
            style={{ width: 80 }}
          >
            Add
          </Button>
        </Space>
        <Divider style={{ margin: "8px 0" }} />
        {menu}
      </>
    );
  };

  return (
    <Select
      prefix={selectPrefix}
      value={value}
      allowClear
      showSearch
      optionFilterProp="label"
      options={options}
      placeholder={selectPlaceholder}
      popupRender={popupRender}
      onChange={onChange}
    />
  );
};

export default DropdownWithAdd;
