import React, { useCallback, useEffect, useMemo, useState } from "react";
import { Form, Input, Button, Typography, Space, Alert, Flex } from "antd";
import { useAuth } from "../../assistant/Contexts/AuthContext";
import { useNavigate } from "react-router-dom";
import Logo from "../../shared/components/Logo";
import { generateWordDocWithContents, insertMarkdownAsHtml } from "../assistant";
import Card from "../../shared/components/Card";
import { useApiClient } from "../../shared/hooks/useApiClient";
import DropdownWithAdd from "./DropdownWithAdd";
import { BookOutlined, SendOutlined } from "@ant-design/icons";
import { GradientButton } from "../../shared/components/GradientButton";

const { Title, Text } = Typography;
const DEFAULT_NEW_TOPIC_ID = "";

const AIAssistantForm = () => {
  const [form] = Form.useForm();
  const [myTopics, setMyTopics] = useState([]);
  const [selectedTopicId, setSelectedTopicId] = useState(undefined);
  const [prompt, setPrompt] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isExtracting, setIsExtracting] = useState(false);
  const [isGenerationSuccess, setIsGenerationSuccess] = useState(true);
  const { logout, username } = useAuth();
  const navigate = useNavigate();
  const apiClient = useApiClient();

  const topicOptions = useMemo(() => {
    return myTopics.map((topic) => ({ label: topic.topic, value: topic.topicId }));
  }, [myTopics]);

  const shouldDisableAddNewTopic = useMemo(() => {
    return myTopics.filter((tp) => tp.topicId === DEFAULT_NEW_TOPIC_ID).length > 0;
  }, [myTopics]);

  useEffect(() => {
    async function fetchMyTopics() {
      const topics = await apiClient.getMyTopics();
      if (topics.isSuccess) {
        setMyTopics((prev) => [...topics.data, ...prev]);
      }
    }

    fetchMyTopics();
  }, []);

  const handleOnAddNew = useCallback(
    (newVal) => {
      setMyTopics([...myTopics, { topicId: DEFAULT_NEW_TOPIC_ID, topic: newVal }]);
      setSelectedTopicId(DEFAULT_NEW_TOPIC_ID);
      form.setFieldValue("topic", DEFAULT_NEW_TOPIC_ID);
    },
    [setMyTopics, myTopics, DEFAULT_NEW_TOPIC_ID, setSelectedTopicId, form]
  );

  const handleOnTopicChange = useCallback(
    (val) => {
      setSelectedTopicId(val);
    },
    [setSelectedTopicId]
  );

  async function handleExtraction() {
    try {
      setIsExtracting(true);
      const response = await apiClient.extractMyContents(selectedTopicId);
      if (response.isSuccess && response.data) {
        await generateWordDocWithContents(response.data);
      }
    } catch (err) {
      logout();
      navigate("/login");
    } finally {
      setIsExtracting(false);
    }
  }

  async function handleOnFinish() {
    try {
      setIsSubmitting(true);
      const response = await apiClient.postHuggingFace(
        prompt,
        selectedTopicId,
        myTopics.find((topic) => topic.topicId === selectedTopicId)?.topic
      );

      if (response.isSuccess && response.data.choices.length) {
        const { content } = response.data.choices[0].message;
        const { topicId } = response.data;
        await insertMarkdownAsHtml(content);
        form.resetFields(["prompt"]);
        // This is the new topic we just created, and it has no topicId yet, hence we need to update it
        if (selectedTopicId === DEFAULT_NEW_TOPIC_ID) {
          const updatedTopics = [...myTopics];
          for (const updatedTopic of updatedTopics) {
            if (updatedTopic.topicId === DEFAULT_NEW_TOPIC_ID) {
              updatedTopic.topicId = topicId;
              break;
            }
          }
          setMyTopics(updatedTopics);
          setSelectedTopicId(topicId);
          form.setFieldValue("topic", topicId);
        }
      }

      setIsGenerationSuccess(response.isSuccess && response.data.choices.length);
    } catch (error) {
      setIsGenerationSuccess(false);
      logout();
      navigate("/login");
    } finally {
      setIsSubmitting(false);
    }
  }

  return (
    <Flex>
      <Card>
        <Space direction="vertical" style={{ width: "100%", marginBottom: "24px" }}>
          <Logo />
          <Flex gap="small">
            <Title level={5}>Welcome {username}!</Title>
            <Button
              type="link"
              danger
              onClick={() => {
                logout();
                navigate("/login");
              }}
              size="small"
            >
              Log Out
            </Button>
          </Flex>
          <Text type="secondary">What are you looking for right now?</Text>
        </Space>
        <Form form={form} name="assistant" onFinish={handleOnFinish} layout="vertical">
          <Form.Item
            label="Topic"
            name="topic"
            required
            rules={[
              {
                validator: (_, value) => {
                  if (value === undefined) {
                    return Promise.reject("Please select a topic or create one");
                  }
                  return Promise.resolve();
                },
              },
            ]}
          >
            <DropdownWithAdd
              value={selectedTopicId}
              options={topicOptions}
              selectPlaceholder="Select a topic or create one"
              newInputPlaceholder="Create one"
              shouldDisableAddNew={shouldDisableAddNewTopic}
              selectPrefix={<BookOutlined />}
              onAddNew={handleOnAddNew}
              onChange={handleOnTopicChange}
            />
          </Form.Item>
          {selectedTopicId !== DEFAULT_NEW_TOPIC_ID && selectedTopicId && (
            <Form.Item>
              <GradientButton
                loading={isExtracting}
                disabled={isExtracting}
                size="small"
                icon={<BookOutlined />}
                onClick={handleExtraction}
                style={{ maxWidth: "100%", overflow: "hidden" }}
              >
                <span
                  style={{
                    display: "block", // Crucial: make it a block-level element to apply width/overflow
                    whiteSpace: "nowrap",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                  }}
                >
                  Extract {myTopics.find((tp) => tp.topicId === selectedTopicId)?.topic}
                </span>
              </GradientButton>
            </Form.Item>
          )}
          <Form.Item
            label="Prompt"
            name="prompt"
            rules={[{ required: true, message: "Please input your prompt!" }]}
          >
            <Input.TextArea
              rows={7}
              placeholder="Your prompt here"
              onChange={(e) => setPrompt(e.target.value)}
              value={prompt}
            />
          </Form.Item>
          <Form.Item>
            <GradientButton
              type="primary"
              htmlType="submit"
              loading={isSubmitting}
              disabled={isSubmitting}
              block
              size="large"
              icon={<SendOutlined />}
            >
              Generate
            </GradientButton>
            {!isGenerationSuccess && !isSubmitting && (
              <Alert
                message="Operation Failed!"
                description="Oop ! Operation failed! Please try again later."
                type="error"
                showIcon
                style={{ marginTop: 8 }}
              />
            )}
          </Form.Item>
        </Form>
      </Card>
      <div
        id="response"
        style={{
          display: "none",
          textAlign: "justify",
          padding: 32,
          height: "calc(100vh - 24px)",
          width: "calc(100vw - 400px)",
          overflowY: "auto",
        }}
      />
    </Flex>
  );
};

export default AIAssistantForm;
